@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@100;200;300;400;500;600;700;800&display=swap');
* {
    box-sizing: border-box;
    border: none;
  }
  body{
    overflow: hidden;
  }
  

.login-form-page-container {
    display: flex;
    width: 100%;
  }
  
  .login-form-left-section {
    
    min-width: 582px;
    background-color: black;
    color: white;
    padding: 50px;
    box-sizing: border-box;
  }
  .login-head{
    display: flex;
    justify-content: center;
    margin-top: 300px;
  }
  .login-title{
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 700;
    font-size: 72px;
    line-height: 88px;
  }
  .login-form-right-container{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
  }
.login-form-container {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: center;
    background-color: #F5F5F5;
    color: black;
    height: 100vh;
    padding-right: 35%;
    padding-bottom: 30%;
    box-sizing: border-box;
    min-width: 938px;
  }
  .login-form-input-container{
    display: grid;
    min-width: 140%;
    background-color: #FFFFFF;
    border-radius: 20px;
    margin-top: 20px;
    padding: 20px;
    margin-bottom: 20px;
  }
  .login-form-heading {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 700;
    font-size: 36px;
    line-height: 44px;
    color: #000000;
    margin-right: 22%;
    margin-top: 260px;
    margin-bottom: -5%;
  }
  
  .login-form-subheading {
    margin-bottom: 1rem;
    font-family: 'Lato';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    color: #000000;
    margin-top: 30px;
    margin-right: 16%;
  }
  .login-form-google-logo {
    display: inline-block;
    width: 20px;
    height: 14px;
    margin-right: 5px;
    background-image: url("/public/images/google.png");
    background-size: contain;
    background-repeat: no-repeat;
  }
  
  .login-form-apple-logo {
    display: inline-block;
    width: 20px;
    height: 14px;
    margin-right: 5px;
    background-image: url("/public/images/applelogo.png");
    background-size: contain;
    background-repeat: no-repeat;
  }
  
  .login-form-buttons {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    width: auto;
    margin-right: -17%;
  }
  
  .login-form-button {
    background-color: white;
    color:  #858585;
    ;
    border: none;
    padding: 0.5rem 1rem;
    border-radius: 0.3rem;
    cursor: pointer;
    transition: all 0.3s ease;
  }
  
  .login-form-button:hover {
    background-color: #e6e6e6;
  }
  
  .login-form-google {
    margin-right: 0.5rem;
    margin-top: 0.5rem;
    background: #FFFFFF;
    border-radius: 10px;
  }
  
  .login-form-apple {
    margin-left: 0.5rem;
    margin-top: 0.5rem;
    background: #FFFFFF;
    border-radius: 10px;
  }
  
  .login-form-input-group {
    display: flex;
    flex-direction: column;
    margin-bottom: 1rem;
  }
  
  /* Add top margin to first input group */
  .login-form-input-group:first-child {
    margin-top: 1rem;
  }
  
  label {
    margin-bottom: 0.5rem;
    font-family: 'Lato';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    color: #000000;
  }
  
  input {
    padding: 0.5rem;
    border-radius: 10px;
    background-color: #EAEAEA;
    border: none;
    font-size: 1rem;
  }
  
  .login-form-forgot-password {
    margin-top: 0.5rem;
    align-self: flex-start;
    text-decoration: none;
    font-family: 'Lato';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    color: #346BD4;
}
  
  .login-form-submit-button {
    background-color: #000000;
    color: #FFFFFF;
    border: none;
    padding: 0.5rem 1rem;
    border-radius: 10px;
    cursor: pointer;
    transition: all 0.3s ease;
    margin-top: 1rem;
    width: -webkit-fill-available;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    text-align: center;

  }
  
  .login-form-register-link {
    margin-top: 1rem;
    margin-right: -4.5rem;
    color: #858585;

  }
  
  .login-form-register-link a {
    color:#346BD4;
    text-decoration: none;
  }
  
 
 