  @media only screen and (max-width: 600px) {
    .recharts-legend-wrapper {
      position: relative;
      top: auto;
      right: auto;
      text-align: center;
    }
    
    .recharts-legend-item-text {
      margin-left: 5px;
      font-size: 12px;
    }
    
    .recharts-legend-item-dot {
      width: 8px;
      height: 8px;
      margin-right: 3px;
      border-width: 1px;
    }
    
    .dropdown-container {
      flex-direction: column;
    }
    
    .dropdown-selected {
      margin-right: 0;
      margin-bottom: 5px;
    }
    
    .dropdown-icon {
      width: 15px;
      height: 15px;
      margin: 0;
      background-size: 100%;
    }
    .chart-container{
    
        box-shadow: 0px 2px 6px rgba(0, 0, 0, 0);
      }
  }
  
  
  