.schedules-container {
    margin: 20px;
  }
  
  .schedules-list {
    list-style-type: none;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .schedule {
    width: 300px;
    margin-bottom: 20px;
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.2);
    border-radius: 5px;
    overflow: hidden;
    display: flex;
    align-items: center;
  }
  
  .schedule-details {
    padding: 20px;
    text-align: center;
  }
  
  .schedule-title {
    margin: 0;
    font-size: 18px;
  }
  
  .schedule-date {
    margin: 5px 0;
    color: gray;
    font-size: 14px;
  }
  
  .schedule-time {
    margin: 0;
    font-size: 14px;
  }
  