.schedule-card {
    background-color: #FFFFFF;
    border-radius: 20px;
    padding: 20px;
    box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.15);
    width: 480px;
    margin: 0 auto;
  }
  
  .schedule-card h2 {
    font-family: 'Montserrat';
    font-weight: 700;
    font-size: 18px;
    margin-top: 24px;
    
  }
  
  .schedule-card ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }
  
  .schedule-card li {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 16px;
  }
  
  .schedule-icon {
    width: 8px;
    height: 100%;
    margin-right: 16px;
  }
  
  .schedule-details {
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex: 1;
  }
  
  .meeting-time {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    
    color: #555555;
  }
  
  .meeting-title {
    font-family: 'Lato';
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  
  color: #999999;
  }
  .meeting-location{
  font-family: 'Lato';
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  
  /* icon */
  
  color: #999999;
  }
  .schedule-header {
    display: flex;
    justify-content: space-between;
    align-items: center
  }
  
  .see-all-link a {
    font-family: 'Montserrat';
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  
  text-decoration: none;
  color: #858585;
  }

  .schedule-card {
    position: absolute;
    bottom: 20px;
    right: 110px;
  }
  @media (max-width: 600px) {
    .schedule-card {
      max-width: 100%;
      position: fixed;
      bottom: 50px;
      right: 0;
      left: 0;
      margin: 0;
      padding: 10px;
      border-radius: 0;
      box-shadow: none;
    }
    
    .schedule-header {
      flex-direction: column;
      align-items: flex-start;
      margin-bottom: 10px;
    }
    
    .see-all-link a {
      margin-right: 0;
      margin-top: 5px;
    }
    
    .schedule-list {
      margin: 0;
      padding: 0;
    }
    
    .schedule-item {
      border-left: none;
      margin-bottom: 8px;
    }
    
    .meeting-title, .meeting-time, .meeting-location {
      font-size: 12px;
    }
  }
  