.dashboard {
  display: grid;
    grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
    gap: 2.0rem;
    margin: 1rem;
    margin-left: 21.5rem;
    padding-right: 105px;
    margin-bottom: 12px;
    margin-top: -9px;
}

.dashboard__card {
  background-color: #ffffff;
  box-shadow: 0px 4px 16px rgba(0, 0, 0, 0.08);
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: revert;
  padding: 1rem;
}

.dashboard__cardHeader {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin-bottom: 1rem;
}

.dashboard__cardLogo {
  font-size: 2rem;
  
}
h3{
  margin-left:30px;
}

p{
  margin-left: 30px;
}
.dashboard__cardLogo {
  stroke: rgba(0, 0, 0, 0.8);
    stroke-width: 40;
    fill: none;
}
.dashboard__cardCount{
  font-family: 'Open Sans';
font-style: normal;
font-weight: 700;
font-size: 24px;
margin-top: -14px;
/* identical to box height */


color: #000000;
}
.dashboard__cardTitle{
  font-family: 'Lato';
font-style: normal;
font-weight: 400;
font-size: 14px;
line-height: 17px;
/* identical to box height */


color: #000000;
}