.sidebar {
    background: #000000;
    border-radius: 30px;
    width: 250px;
    position: fixed;
    bottom: 4px;
    left: 4px;
    overflow-x: hidden;
    overflow-y: hidden;
    height: -webkit-fill-available;
    padding-top: 20px;
    margin-top: 15px;
  }
  
  .sidebar__header {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 30px;
  }
  
  .sidebar__title {
    margin-right: 95px;
    color: #FFFFFF; /* Updated */
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 700;
    font-size: 36px;
    line-height: 44px;
    
    }
  
  .sidebar__menu {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  .sidebar__menuItem {
    padding: 10px 20px;
    border-left: 4px solid transparent;
    display: flex;
    align-items: center;
  }
  
  .sidebar__menuItem--active {
    border-left: 4px solid #007bff;
  }
  
  .sidebar__menuItemLink {
    text-decoration: none;
    color: #FFFFFF; /* Updated */
    font-size: 18px;
    display: flex;
    align-items: center;
  }
  
  .sidebar__menuIcon {
    margin-right: 10px;
    font-size: 20px;
  }
  
  .sidebar__footer {
    margin-top: auto;
    display: flex;
    flex-direction: column;
    margin-top: 150px;
    font-family: 'Montserrat';
font-style: normal;
font-weight: 400;
font-size: 14px;
line-height: 17px;
  }
  
  
  
  
  .sidebar__help, .sidebar__contact {
    display: flex;
    align-items: center;
    justify-content: start;
    margin: 10px 33px;
    color: #ffffff;
  }
  .sidebar__menuIcon--dashboard,.sidebar__menuIcon--transactions,.sidebar__menuIcon--schedules,.sidebar__menuIcon--users,.sidebar__menuIcon--settings{
  
    stroke: #ffffff;
    stroke-width: 20;
    fill: none;
    margin-top: -40px;
  }

  h6{
    margin-left: 10px;
    margin-top: 0px;
    font-family: 'Montserrat';
   font-style: normal;
   font-weight: 700;
   font-size: 18px;
   line-height: 22px;
  }
  
  