.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 50px;
  }
  
  .header__left {
    margin-right: 50px;
  }
  
  .header__title {
    font-size: 24px;
    font-weight: 700;
    
    margin-left: 300px;
  }
  
  .header__right {
    display: flex;
    align-items: center;
    margin-right: 80px;
  }
  
  .header__icons {
    display: flex;
    margin-left: 40px;
  }
  
  .header__icon {
    font-size: 24px;
    margin-left: 20px;
    color: #a0a0a0;
  }
  
  .header__icon--notification {
    stroke: #000000;
    stroke-width: 70;
    fill: none;
    height: 30px;
  }
  
  .header__search {
    position: relative;
    display: flex;
    align-items: center;
    margin-right: -20px;
  }
  
  .header__searchInput {
    padding: 8px 28px 8px 12px;
    border: none;
    border-radius: 20px;
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.1);
    font-size: 14px;
    font-weight: 400;
    width: 100%;
  }
  
  .header__searchInput:focus {
    outline: none;
  }
  
  .header__searchIcon {
    position: absolute;
    left: 165px;
    top: 50%;
    transform: translateY(-50%);
    color: #a0a0a0;
    font-size: 18px;
  }
 
  
  @media only screen and (max-width: 883px) {
    .header__searchIcon {
       display: none;
       
    }
    .header__search ::placeholder{
        display: none;
    }
  }
  