
body,html{
     height: 100%;
     margin: 0;
     padding: 0;
    
}
  .footer-section > * {
    flex-basis: calc(50% - 20px);
  }
  
.card {
    
    width: 400px;
    height: 100px;
    background-color: #ffffff;
    border-radius: 20px;
    box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.15);
  }
  
  .card-body {
    
    padding-bottom: 220px;
  }
  
  .card-title {
    margin-bottom: 8px;
    margin-top: 20px;
    margin-left: 30px;
    white-space: nowrap;
  }
  
  .dropdown-s{
    position: relative;
    display: inline-block;
    margin-left: 0px;
    margin-top: 10px;
  }
  
  .dropdown-s select {
    width: 126px;
   
    font-family: "Montserrat";
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 15px;
    border: none;
    /* identical to box height */
  
    /* Secondary Text */
  
    color: #858585;
    margin-left: 170px;
    appearance: none;
    outline: none;
    cursor: pointer;
  }
  
  .dropdown-s::after {
    content: "";
    position: absolute;
    border-left: 6px solid transparent;
    border-right: 6px solid transparent;
    border-top: 6px solid #333;
    right: 39px;
    top: 50%;
    transform: translateY(-50%);
    pointer-events: none;
  }
  canvas{
    margin-left: 30px;
    margin-top: -70px;
  }
  .dropdown select:focus {
    box-shadow: 0 0 0 2px rgba(0, 0, 0, 0.1);
  }
  @media (max-width: 327px){
    html,body{
        overflow: auto;
    }
  }