.transactions-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin: 20px;
  }
  
  .transactions-list {
    list-style-type: none;
    padding: 300px;
  }
  .transaction-list li{
    margin-right: 400px;
  }
  .transaction {
    margin: 0 20px;
  }
  
  .transaction-name {
    font-weight: bold;
  }
  
  .transaction-amount {
    margin-left: 5px;
    margin-right: 5px;
  }
  
  .transaction-date {
    color: gray;
  }
  