.users-container {
    margin: 20px;
  }
  
  .users-list {
    list-style-type: none;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  
  .user {
    width: 300px;
    margin-bottom: 20px;
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.2);
    border-radius: 5px;
    overflow: hidden;
    display: flex;
    align-items: center;
  }
  
  .user-avatar {
    width: 80px;
    height: 80px;
    object-fit: cover;
    margin-right: 10px;
    border-radius: 50%;
  }
  
  .user-name {
    margin: 0;
    font-size: 18px;
  }
  
  .user-email {
    margin: 0;
    color: gray;
    font-size: 14px;
  }
  